body, h3, p {
  margin: 0;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.header {
  background-color: #fff;
  width: 100%;
  box-shadow: 0 2px 4px #0000000d;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  height: 40px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
  padding: 0;
  position: relative;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav__link:hover, .nav__link.is-active {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.main {
  grid-template-columns: repeat(1, auto);
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, auto);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, auto);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, auto);
  }
}

.product-card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform, color, all .3s linear;
  display: flex;
}

.product-card:hover {
  transform: scale(1.2);
}

.product-card__image {
  width: 160px;
  height: 134px;
}

.product-card__content {
  width: 100%;
}

.product-card__title {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card:hover .product-card__title {
  color: #34568b;
}

.product-card__product-code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.product-card__review-wrapper {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.product-card__review {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.product-card__price-wrapper {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.product-card__text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.product-card__amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__button {
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin-top: 16px;
  padding: 11px 70px 11px 69px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: background-color, color, border, all .3s linear;
  display: flex;
}

.product-card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border-color: #00acdc;
}

.stars {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 .stars__star:first-child, .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
